.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
.App.dark .ui.celled.list {
  background-color: #777272;
}
.App.dark .ui.celled.list span,
.App.dark .ui.celled.list i {
  color:#fff;
}
.App.dark button.ui.button {
  background-color: #000;
  color: #fff;
}
.theme {
  display: flex;
  align-items: center;
}
.theme label {
  display: inline-block;
  margin-right: 10px;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
